import React, { useState } from 'react'
import { Input, Label } from '../../commanComponent'
import { calculateFilledPercentage } from '../../../utils/common';
import { validateSchema } from '../../../utils/ValidationHelper';
import { addReferenceDetailsSchema } from '../../../utils/ValidationSchema';

const ReffrenceDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick } = props
    const [errors, setErrors] = useState({});
     const [isDisabled,setIsDisabled]=useState(true)
    const [referenceDetails, setReferenceDetails] = useState(
        {
            "first_reference_name": '',
            "first_reference_no": '',
            "first_reference_email": '',
            "second_reference_name": '',
            "second_reference_no": '',
            "second_reference_email": '',
        }
    );

    const percentage = calculateFilledPercentage(referenceDetails);

    const handleFormData = async (e) => {
        errors && setErrors({});
        const { name, value } = e.target
        setReferenceDetails({ ...referenceDetails, [name]: value })
    };

    const onSubmitForm = async () => {
        const validationResponse = await validateSchema(addReferenceDetailsSchema, referenceDetails);
        if (validationResponse.isValid) {
            setErrors({});
            // addReferenceDetails(referenceDetails)
        } else {
            setErrors(validationResponse.errors);
        }
    };
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="reffrenceDetails"
            >

                <div class="offcanvas-header">
                    <h5 class="offcanvas-title  fs-6 fw-bold text-primary">Reference Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("referenceDetail")}
                    />

                </div>
                <div class="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <div className="detailMain">
                            <div className="personalDetail d-flex align-items-center">
                                <span onClick={() => handleNextPreOnClick("socialDetail")}>
                                    <img src="images/previousIcon-blue.svg" width="24" alt="Previous" />
                                </span>
                                <div className="personalLeft ms-2">
                                    <span>Reference Details</span>
                                    <p>Lorem Ipsum is simply dummy text of the printing</p>
                                    <strong>Status: <b className="text-danger">1 of 7</b></strong>
                                </div>
                            </div>
                            <div className="personalDetail d-flex align-items-center">
                                <div className="personalLeft border-0 me-2 text-end">
                                    <span>NEXT</span>
                                    <p className="m-0">Work Experience Details</p>
                                </div>
                                <span onClick={() => handleNextPreOnClick("workExperienceDetail")}>
                                    <img src="images/nextIconblue.svg" width="24" alt="Next" />
                                </span>
                            </div>
                        </div>

                        <div className="row">
                            <label style={{ color: "darkblue", fontWeight: "bolder" }}>FIRST REFERENCE</label>
                            <br />
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <label htmlFor="">Name</label>
                                    <Input
                                        type="text"
                                        name='first_reference_name'
                                        value={referenceDetails?.first_reference_name}
                                        onChange={(e) => handleFormData(e)}
                                        className="form-control"
                                        placeholder="Enter First Reference Name"
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <label htmlFor="">Contact No.</label>
                                    <Input
                                        type="text"
                                        name='first_reference_no'
                                        value={referenceDetails?.first_reference_no}
                                        onChange={(e) => handleFormData(e)}
                                        className="form-control"
                                        placeholder="Enter First Reference Contact No."
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <label htmlFor="">Email</label>
                                    <Input
                                        type="text"
                                        name='first_reference_email'
                                        value={referenceDetails?.first_reference_email}
                                        onChange={(e) => handleFormData(e)}
                                        className="form-control"
                                        placeholder="Enter First Reference Email"
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                            <br /><br />
                            <label style={{ color: "darkblue", fontWeight: "bolder" }}>SECOND REFERENCE</label>
                            <br />
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <label htmlFor="">Name</label>
                                    <Input
                                        type="text"
                                        name='second_reference_name'
                                        value={referenceDetails?.second_reference_name}
                                        onChange={(e) => handleFormData(e)}
                                        className="form-control"
                                        placeholder="Enter Second Reference Name"
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <label htmlFor="">Contact No.</label>
                                    <Input
                                        type="text"
                                        name='second_reference_no'
                                        value={referenceDetails?.second_reference_no}
                                        onChange={(e) => handleFormData(e)}
                                        className="form-control"
                                        placeholder="Enter Second Reference Contact No."
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <label htmlFor="">Email</label>
                                    <Input
                                        type="text"
                                        name='second_reference_email'
                                        value={referenceDetails?.second_reference_email}
                                        onChange={(e) => handleFormData(e)}
                                        className="form-control"
                                        placeholder="Enter Second Reference Email"
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                           
                        </div>
                    </div>

                </div>
                <div class="offcanvas-footer">
                    {/* <div class="d-flex align-items-center justify-content-end gap-3">
                        <a href="#" class="btn btn-org-gradient"> update</a>
           
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default ReffrenceDetail
