import React, { useState } from 'react'
import { useCustomTranslation } from '../../../translation';
import { Input, Label } from '../../commanComponent';
import { CustomError } from '../../globalComponents';
import { calculateFilledPercentage } from '../../../utils/common';
import { validateSchema } from '../../../utils/ValidationHelper';
import { addBankDetailsSchema } from '../../../utils/ValidationSchema';

const BankDetail = (props) => {
    const { openModel, handleOpenClose ,handleNextPreOnClick} = props

    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({});
    const [isDisabled,setIsDisabled]=useState(true)
    const [bankDetails, setBankDetails] = useState(
        {
            "ifsc_code": '',
            "bank_name": '',
            "branch_name": '',
            "bank_account_holder_name": '',
            "bank_account_no": '',
            "nominee_name": '',
            "relation_with_nominee": '',
        }
    );

    const percentage = calculateFilledPercentage(bankDetails);

    const handleFormData = async (e) => {
        errors && setErrors({});
        const { name, value } = e.target
        setBankDetails({ ...bankDetails, [name]: value })
    };

    const onSubmitForm = async () => {
        const validationResponse = await validateSchema(addBankDetailsSchema, bankDetails);
        if (validationResponse.isValid) {
            setErrors({});
            // addBankDetails(bankDetails)
        } else {
            setErrors(validationResponse.errors);
        }
    };
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="bankDetails"
            >

                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Bank Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("bankDetail")}
                    />
                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <div className="detailMain">
                            <div className="personalDetail d-flex align-items-center">
                                <span onClick={()=>handleNextPreOnClick("educationDetail")}><img src="images/previousIcon-blue.svg" width="24" alt="" /></span>
                                <div className="personalLeft ms-2">
                                    <span>Bank Details</span>
                                    <p>Lorem Ipsum is simply dummy text of the printing </p>
                                    <strong>Status : <b className="text-success">4 of 4</b> </strong>
                                </div>
                            </div>
                            <div className="personalDetail d-flex align-items-center">
                                <div className="personalLeft border-0 me-2 text-end">
                                    <span>NEXT </span>
                                    <p className="m-0">Social Details</p>
                                </div>
                                <span onClick={()=>handleNextPreOnClick("socialDetail")}><img src="images/nextIconblue.svg" width="24" alt="" /></span>
                            </div>
                        </div>
                        <div className="mb-1">
                            <div className="row p-2">
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <Label text={t("labels.ifscCode")} required={true} />
                                        <Input
                                            type="text"
                                            value={bankDetails?.ifsc_code}
                                            name="ifsc_code"
                                            onChange={(e) => handleFormData(e)}
                                            placeholder={t("placeholders.ifscCode")}
                                            readOnly={isDisabled}
                                        />

                                        {errors.ifsc_code && <CustomError
                                            text={errors.ifsc_code}
                                        />}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <Label text={t("labels.bankName")} required={true} />
                                        <Input
                                            type="text"
                                            value={bankDetails?.bank_name}
                                            name="bank_name"
                                            onChange={(e) => handleFormData(e)}
                                            placeholder={t("placeholders.bankName")}
                                            readOnly={isDisabled}
                                        />

                                        {errors.bank_name && <CustomError
                                            text={errors.bank_name}
                                        />}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <Label text={t("labels.branchName")} required={true} />
                                        <Input
                                            type="text"
                                            value={bankDetails?.branch_name}
                                            name="branch_name"
                                            onChange={(e) => handleFormData(e)}
                                            placeholder={t("placeholders.branchName")}
                                            readOnly={isDisabled}
                                        />
                                        {errors.branch_name && <CustomError
                                            text={errors.branch_name}
                                        />}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="" className='text-primary'>Bank Account Holder Name</label>
                                        <Input
                                            type="text"
                                            name="bank_account_holder_name"
                                            value={bankDetails?.bank_account_holder_name}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter Bank Account Holder Name"
                                            readOnly={isDisabled}
                                        />{errors.bank_account_holder_name && <CustomError
                                            text={errors.bank_account_holder_name}
                                        />}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="" className='text-primary'>Bank Account No.</label>
                                        <Input
                                            type="text"
                                            name="bank_account_no"
                                            value={bankDetails?.bank_account_no}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter Bank Account No."
                                            readOnly={isDisabled}
                                        />{errors.bank_account_no && <CustomError
                                            text={errors.bank_account_no}
                                        />}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="" className='text-primary'>Full Name of Nominee</label>
                                        <Input
                                            type="text"
                                            name="nominee_name"
                                            value={bankDetails?.nominee_name}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter Name of Nominee"
                                            readOnly={isDisabled}
                                        />{errors.nominee_name && <CustomError
                                            text={errors.nominee_name}
                                        />}
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="" className='text-primary'>Relationship with Nominee</label>
                                        <Input
                                            type="text"
                                            name="relation_with_nominee"
                                            value={bankDetails?.relation_with_nominee}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Select Relationship with nominee"
                                            readOnly={isDisabled}
                                        />{errors.relation_with_nominee && <CustomError
                                            text={errors.relation_with_nominee}
                                        />}
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end">
                        <button className="btn btn-primary">Update</button>
                    </div>
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default BankDetail
