import React, { useEffect, useState } from 'react'
import { Input, Label } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'
import { CustomError } from '../../globalComponents'
import moment from 'moment'
import { calculateFilledPercentage } from '../../../utils/common'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addFamilyDetailsSchema } from '../../../utils/ValidationSchema'

const FamilyDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick,profileData } = props
    const { t } = useCustomTranslation()
    const { addFamilyDetails, onClickActive } = props;
    const [errors, setErrors] = useState({});
    const [isDisabled,setIsDisabled]=useState(true)
    const [familyDetails, setFamilyDetails] = useState(
        {
            "fathers_name": "",
            "fathers_dob": "",
            "mothers_name": "",
            "mothers_dob": "",
            "spouse_name": "",
            "spouse_dob": "",
            "spouse_aadhaar": "",
            "first_child_name": "",
            "first_child_dob": "",
            "second_child_name": "",
            "second_child_dob": "",
        }
    );

    const percentage = calculateFilledPercentage(familyDetails);

    const handleFormData = async (e) => {
        errors && setErrors({});
        const { name, value } = e.target
        setFamilyDetails({ ...familyDetails, [name]: value })
    };

    const onSubmitForm = async () => {
        const validationResponse = await validateSchema(addFamilyDetailsSchema, familyDetails);
        if (validationResponse.isValid) {
            setErrors({});
            addFamilyDetails(familyDetails)

        } else {
            setErrors(validationResponse.errors);
        }
    };


    useEffect(()=>{
        setFamilyDetails(
            {
                "fathers_name": profileData?.fathers_name,
                "fathers_dob": profileData?.dob,
                "mothers_name": profileData?.mothers_name,
                "mothers_dob": profileData?.mothers_dob,
                "spouse_name": profileData?.spouse_name,
                "spouse_dob": profileData?.spouse_dob,
                "spouse_aadhaar":  profileData?.spouse_aadhaar,
                "first_child_name": profileData?.first_child_name,
                "first_child_dob": profileData?.first_child_dob,
                "second_child_name":  profileData?.second_child_name,
                "second_child_dob": profileData?.second_child_dob,
            })
    },[profileData])
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="familyDetails"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Family Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("familyDetail")}
                    />

                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <div className="detailMain">
                            <div className="personalDetail d-flex align-items-center">
                                <span onClick={() => handleNextPreOnClick("personalDetail")}>
                                    <img src="images/previousIcon-blue.svg" width="24" alt="" />
                                </span>
                                <div className="personalLeft ms-2">
                                    <span>Family Details </span>
                                    <p>Lorem Ipsum is simply dummy text of the printing</p>
                                    <strong>Status : <b className="text-success">2 of 4</b> </strong>
                                </div>
                            </div>
                            <div className="personalDetail d-flex align-items-center">
                                <div className="personalLeft border-0 me-2 text-end">
                                    <span>NEXT </span>
                                    <p className="m-0">Educational Details </p>
                                </div>
                                <span onClick={() => handleNextPreOnClick("educationDetail")}>
                                    <img src="images/nextIconblue.svg" width="24" alt="" />
                                </span>
                            </div>
                        </div>

                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.fatherName")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.fatherName")}
                                        value={familyDetails?.fathers_name}
                                        name="fathers_name"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />

                                    {errors.fathers_name && <CustomError
                                        text={errors.fathers_name}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.fatherDOB")} />
                                    <Input
                                        type="date"
                                        value={familyDetails?.fathers_dob}
                                        name="fathers_dob"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                        max={moment()
                                            .subtract(18, 'years')
                                            .format('YYYY-MM-DD')}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.motherName")} required={true} />
                                    <Input
                                        type="text"
                                        value={familyDetails?.mothers_name}
                                        name="mothers_name"
                                        onChange={(e) => handleFormData(e)}
                                        placeholder={t("placeholders.motherName")}
                                        readOnly={isDisabled}
                                    />

                                    {errors.mothers_name && <CustomError
                                        text={errors.mothers_name}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.motherDOB")} />
                                    <Input
                                        type="date"
                                        value={familyDetails?.mothers_dob}
                                        name="mothers_dob"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                        max={moment()
                                            .subtract(18, 'years')
                                            .format('YYYY-MM-DD')}
                                    />

                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.spouseName")} />
                                    <Input
                                        type="text"
                                        value={familyDetails?.spouse_name}
                                        name="spouse_name"
                                        onChange={(e) => handleFormData(e)}
                                        placeholder={t("placeholders.spouseName")}
                                        readOnly={isDisabled}
                                    />

                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.spouseDOB")} />
                                    <Input
                                        type="date"
                                        value={familyDetails?.spouse_dob}
                                        name="spouse_dob"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                        max={moment()
                                            .subtract(18, 'years')
                                            .format('YYYY-MM-DD')}
                                    />

                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.spouseAadharCardNo")} />
                                    <Input
                                        type="text"
                                        value={familyDetails?.spouse_aadhaar}
                                        name="spouse_aadhaar"
                                        onChange={(e) => handleFormData(e)}
                                        placeholder={t("placeholders.spouseAadharCardNo")}
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.firstChildName")} />
                                    <Input
                                        type="text"
                                        value={familyDetails?.first_child_name}
                                        name="first_child_name"
                                        onChange={(e) => handleFormData(e)}
                                        placeholder={t("placeholders.firstChildName")}
                                        readOnly={isDisabled}
                                    />

                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.firstChildDOB")} />
                                    <Input
                                        type="date"
                                        value={familyDetails?.first_child_dob}
                                        name="first_child_dob"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}

                                    />

                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.secondChildName")} />
                                    <Input
                                        type="text"
                                        value={familyDetails?.second_child_name}
                                        name="second_child_name"
                                        onChange={(e) => handleFormData(e)}
                                        placeholder={t("placeholders.secondChildName")}
                                        readOnly={isDisabled}
                                    />

                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.secondChildDOB")} />
                                    <Input
                                        type="date"
                                        value={familyDetails?.second_child_dob}
                                        name="second_child_dob"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />
                                </div>
                            </div>

                        </div>
                    </div>

                </div>
                <div className="offcanvas-footer">
                    {/* <div className="d-flex align-items-center justify-content-end">
                        <a href="#" className="btn btn-org-gradient"> Edit</a>
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default FamilyDetail
