import React from 'react'
import Lottieplayer from '../commanComponent/Lottieplayer'
import businesshiring from "../../images/business-hiring.json"
import Lottie from 'react-lottie-player'
import { CustomButton } from '../globalComponents'

const ProfileEditDocCard = () => {
    return (
        <>
            <div className="cardBox mb-3 createNewMrf">
                <div className="row align-items-center">
                    <div className="col-md-9 col-xl-10">
                        <div className="row align-items-center">
                            <div className="col-md-3">

                                <Lottie
                                    loop
                                    // src="../images/star-icon.json"
                                    animationData={businesshiring}
                                    background="transparent"
                                    speed="1"
                                    play
                                    style={{ width: "100%", height: "124px" }}
                                    autoPlay
                                />



                            </div>
                            <div className="col-md-9">
                                <div className="cardHead pb-0">
                                    <h3>Edit <span>Documents</span></h3>
                                    <p className="mb-0">Lorem Ipsum is simply dummy text of the printing and typesetting
                                        industry. Lorem Ipsum has been the industry's</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-3 col-xl-2">
                        <CustomButton  text={"Edit Document"} classNames="btn btn-primary gradientBg w-100"></CustomButton>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ProfileEditDocCard
