import React, { useEffect, useState } from 'react'
import { Icon, Input, Label } from '../../commanComponent'
import { CustomError } from '../../globalComponents'
import EducationDetailsTable from '../../employee/onboarding-process/EducationDetailsTable'
import ApproveRejectModal from '../ApproveRejectModal'
import { useCustomTranslation } from '../../../translation'
import { validateSchema } from '../../../utils/ValidationHelper'
import { addEducationDetailsSchema } from '../../../utils/ValidationSchema'
import { calculateFilledPercentage } from '../../../utils/common'

const EducationDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick, profileData } = props
    const { t } = useCustomTranslation()
    const [educationDetails, setEductionDetail] = useState([])
    const [isDisabled, setIsDisabled] = useState(true)
    const [isShowDeleteModal, setIsShowDeleteModal] = useState(false)
    const [deleteIndex, setDeleteIndex] = useState('')
    const [errors, setErrors] = useState({})

    const percentage = calculateFilledPercentage(educationDetails);

    const handleFromChange = (input, fieldName, index) => {
        const name = fieldName || input.target.name;
        const value = input.target ? input.target.value : input;

        setEductionDetail((prevData) => {
            let updatedData = [...prevData]; // Create a copy of the array

            if (index !== undefined && Array.isArray(prevData)) {
                updatedData[index] = {
                    ...prevData[index],
                    [name]: value,
                };
            } else {
                updatedData = {
                    ...prevData,
                    [name]: value,
                };
            }

            return updatedData;
        });

        setErrors((prevErrors) => ({
            ...prevErrors,
            [index !== undefined ? `${index}-${name}` : name]: undefined, // Handle errors for indexed fields
        }));
    };



    const onDeleteEducationDelete = (type) => {
        if (type) {
            const updatedEducationDetail = educationDetails.filter((_, i) => i !== deleteIndex);
            setEductionDetail(updatedEducationDetail);
            setDeleteIndex('')
        }
        toggleDeleteEducationModal()
    };
    const onSubmitForm = () => {
        const params = {
            education_details: educationDetails
        }
        // addEductionDetails(params)
        // onClickActive(4)
    }



    const toggleDeleteEducationModal = (index) => {
        setIsShowDeleteModal(!isShowDeleteModal)
        setDeleteIndex(index)
    }
    const renderedEducationDetail = () => {
        return (
            educationDetails?.map((detail, index) => (
                <div className="detailOne mb-3" key={index}>
                    <span className="updation">Detail {index+1}</span>
                    <div className="p-3">
                        <div className="row">
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={"Educational Class"} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={"Please Enter Educational Class"}
                                        value={detail?.educationalClass}
                                        name="educationalClass"
                                        onChange={(e) => handleFromChange(e, null, index)}
                                        readOnly={isDisabled}
                                    />
                                    {errors[index]?.educationalClass && (
                                        <CustomError text={errors[index].educationalClass} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={"Roll No"} required={true} />
                                    <Input
                                        type="number"
                                        placeholder={"Please Enter roll no"}
                                        value={detail?.rollNo}
                                        name="rollNo"
                                        onChange={(e) => handleFromChange(e, null, index)}
                                        readOnly={isDisabled}
                                    />
                                    {errors[index]?.rollNo && (
                                        <CustomError text={errors[index].rollNo} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={"School Name"} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={"Please Enter School Name"}
                                        value={detail?.schoolName}
                                        name="schoolName"
                                        onChange={(e) => handleFromChange(e, null, index)}
                                        readOnly={isDisabled}
                                    />
                                    {errors[index]?.schoolName && (
                                        <CustomError text={errors[index].schoolName} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={"School/College Board"} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={"Please Enter School/College Board"}
                                        value={detail?.board}
                                        name="board"
                                        onChange={(e) => handleFromChange(e, null, index)}
                                        readOnly={isDisabled}
                                    />
                                    {errors[index]?.board && (
                                        <CustomError text={errors[index].board} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={"Percentage"} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={"Please Enter Percentage"}
                                        value={detail?.percentage}
                                        name="percentage"
                                        onChange={(e) => handleFromChange(e, null, index)}
                                        readOnly={isDisabled}
                                    />
                                    {errors[index]?.percentage && (
                                        <CustomError text={errors[index].percentage} />
                                    )}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={"Year"} required={true} />
                                    <Input
                                        type="month"
                                        placeholder={"Please Enter Year"}
                                        value={detail?.year}
                                        name="year"
                                        onChange={(e) => handleFromChange(e, null, index)}
                                        readOnly={isDisabled}
                                    />
                                    {errors[index]?.year && (
                                        <CustomError text={errors[index].year} />
                                    )}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            ))
        )
    }


    useEffect(() => {
        if (profileData) {
            setEductionDetail(profileData?.education_details)
        }
    }, [profileData])

    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="educationDetails"
            >

                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Education Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("educationDetail")}
                    />

                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <div className="detailMain">
                            <div className="personalDetail d-flex align-items-center">
                                <span onClick={() => handleNextPreOnClick("familyDetail")}>
                                    <img src="images/previousIcon-blue.svg" width="24" alt="" />
                                </span>
                                <div className="personalLeft ms-2">
                                    <span>Education Details</span>
                                    <p>Lorem Ipsum is simply dummy text of the printing</p>
                                    <strong>Status: <b className="text-success">4 of 4</b></strong>
                                </div>
                            </div>
                            <div className="personalDetail d-flex align-items-center">
                                <div className="personalLeft border-0 me-2 text-end">
                                    <span>NEXT</span>
                                    <p className="m-0">Bank Details</p>
                                </div>
                                <span onClick={() => handleNextPreOnClick("bankDetail")}>
                                    <img src="images/nextIconblue.svg" width="24" alt="" />
                                </span>
                            </div>
                        </div>
                        {renderedEducationDetail()}
                    </div>

                </div>
                <div className="offcanvas-footer">
                    {/* <div className="d-flex align-items-center justify-content-end">
                        <a href="#" className="btn btn-org-gradient"> Edit</a>
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
            {isShowDeleteModal && (
                <ApproveRejectModal
                    onApproveReject={onDeleteEducationDelete}
                    isShow={isShowDeleteModal}
                    desc={"You want to delete"}
                    btnTextRight={t("btns.delete")}
                    btnTextLeft={t("btns.cancel")}
                />)
            }
        </>
    )
}

export default EducationDetail
