import React, { useEffect, useState } from 'react'
import { ProfileInfo, ProfileEditDocCard, ProfileDetailCards } from '.'
import {
  ProfileDetail,
  FamilyDetail,
  EducationDetail,
  WorkExpDetail,
  SocialDetail,
  ReffrenceDetail,
  PolicyDetails,
  IdCard,
  DocumentDetail,
  BankDetail,
} from '../modal/profile'
import { useDispatch, useSelector } from 'react-redux'
import { employeeActions } from '../../core/resource/actions'
const Profile = () => {
  const dispatch = useDispatch()
  const {
    getProfilePending,
    getProfileRes,
    getProfileError,
  } = useSelector(state => state.employee)
  const [profileData, setProfileData] = useState()
  const [isOpenModal, setIsOpenModal] = useState({
    personalDetail: false,
    familyDetail: false,
    educationDetail: false,
    workExperienceDetail: false,
    socialDetail: false,
    referenceDetail: false,
    policyDetail: false,
    documentDetail: false,
    bankDetail: false,
    idCard: false,
  })

  useEffect(() => {
    getProfileData()
  }, [])
  /* Get Employee Profile Data*/
  const getProfileData = () => {
    dispatch(employeeActions.getProfileRequest())
  }

  useEffect(() => {
    if (!getProfilePending) {
      if (getProfileError) {
        return;
      }
      if (getProfileRes) {
        if (getProfileRes?.code === 200) {
          setProfileData(getProfileRes?.data[0])
        }
      }
    }
  }, [
    getProfileRes,
    getProfileError,
    getProfilePending
  ]);


  const handleOpenCloseModal = (type) => {
    setIsOpenModal(pre => ({ ...pre, [type]: !pre[type] }))

  }

  const handleNextPreOnClick = (type) => {
    setIsOpenModal(prev => {
      const resetState = Object.keys(prev).reduce((acc, key) => {
        acc[key] = false;
        return acc;
      }, {});
      return { ...resetState, [type]: true };

    });
  };

  return (
    <>
      <ProfileInfo
        profileData={profileData}
      />
      <ProfileEditDocCard />
      <ProfileDetailCards
        handleOpenCloseModal={handleOpenCloseModal}
      />
      {isOpenModal?.personalDetail &&
        <ProfileDetail
          openModel={isOpenModal?.personalDetail}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          profileData={profileData}
        />}
      {isOpenModal?.familyDetail &&
        <FamilyDetail
          openModel={isOpenModal?.familyDetail}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          profileData={profileData}
        />}
      {isOpenModal?.educationDetail &&
        <EducationDetail
          openModel={isOpenModal?.educationDetail}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
          profileData={profileData}
        />}
      {isOpenModal?.bankDetail && <BankDetail
        openModel={isOpenModal?.bankDetail}
        handleOpenClose={handleOpenCloseModal}
        handleNextPreOnClick={handleNextPreOnClick}
      />}
      {isOpenModal?.socialDetail &&
        <SocialDetail
          openModel={isOpenModal?.socialDetail}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
        />}


      {isOpenModal?.referenceDetail &&
        <ReffrenceDetail
          openModel={isOpenModal?.referenceDetail}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
        />}
      {isOpenModal?.workExperienceDetail && <WorkExpDetail
        openModel={isOpenModal?.workExperienceDetail}
        handleOpenClose={handleOpenCloseModal}
        handleNextPreOnClick={handleNextPreOnClick}
      />}

      {isOpenModal?.policyDetail &&
        <PolicyDetails
          openModel={isOpenModal?.policyDetail}
          handleOpenClose={handleOpenCloseModal}
          handleNextPreOnClick={handleNextPreOnClick}
        />}

      {isOpenModal?.documentDetail && <DocumentDetail
        openModel={isOpenModal?.documentDetail}
        handleOpenClose={handleOpenCloseModal}
      />}

      {isOpenModal?.idCard && <IdCard
        openModel={isOpenModal?.idCard}
        handleOpenClose={handleOpenCloseModal}
      />}


    </>
  )
}

export default Profile
