import React, { useEffect, useState } from 'react'
import { Input, Label } from '../../commanComponent'
import { useCustomTranslation } from '../../../translation'
import { calculateFilledPercentage } from '../../../utils/common'
import { CustomError } from '../../globalComponents'
import CustomSelect from '../../commanComponent/CustomSelect'
import { workExperienceType } from '../../../utils/Constants.config'

const WorkExpDetail = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick, profileData } = props

    const { t } = useCustomTranslation()
    const [workExperienceDetails, setWorkExperienceDetails] = useState([])
    const [isDisabled, setIsDisabled] = useState(true)
    const [isShowDeleteModal, setIsShowDeleteModal] = useState(false)
    const [deleteIndex, setDeleteIndex] = useState('')
    const [errors, setErrors] = useState({})
      const [workExperience, setWorkExperience] = useState('')

    const percentage = calculateFilledPercentage(workExperienceDetails);

    const handleFromChange = (input, fieldName, index) => {
        const name = fieldName || input.target.name;
        const value = input.target ? input.target.value : input;

        setWorkExperienceDetails((prevData) => {
            let updatedData = [...prevData]; // Create a copy of the array

            if (index !== undefined && Array.isArray(prevData)) {
                updatedData[index] = {
                    ...prevData[index],
                    [name]: value,
                };
            } else {
                updatedData = {
                    ...prevData,
                    [name]: value,
                };
            }

            return updatedData;
        });

        setErrors((prevErrors) => ({
            ...prevErrors,
            [index !== undefined ? `${index}-${name}` : name]: undefined, // Handle errors for indexed fields
        }));
    };



    const onDeleteWorkExperienceDelete = (type) => {
        if (type) {
            const updatedWorkExperienceDetail = workExperienceDetails.filter((_, i) => i !== deleteIndex);
            setWorkExperienceDetails(updatedWorkExperienceDetail);
            setDeleteIndex('')
        }
        toggleDeleteWorkExperienceModal()
    };
    const onSubmitForm = () => {
        const params = {
            education_details: workExperienceDetails
        }
        // addEductionDetails(params)
        // onClickActive(4)
    }



    const toggleDeleteWorkExperienceModal = (index) => {
        setIsShowDeleteModal(!isShowDeleteModal)
        setDeleteIndex(index)
    }
    const renderedWorkExperienceDetail = () => {
        return (
            workExperienceDetails?.map((detail, index) => (
                <div className="detailOne mb-3" key={index}>
                    <span className="updation">Detail {index + 1}</span>
                    <div className="row">
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.workExperience")} required={true} />
                                <CustomSelect
                                    data={workExperienceType}
                                    placeholder={t("placeholders.workExperience")}
                                    value={workExperience || ''}
                                    handleChange={(selectedOption) => setWorkExperience(selectedOption)}
                                    name="workExperience"
                                />
                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.organizationName")} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.organizationName")}
                                    value={workExperienceDetails?.organizationName}
                                    name="organizationName"
                                    onChange={(e) => handleFromChange(e)}
                                    isDisabled={workExperience?.value === "fresher" ? true : false}
                                />

                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.from")} />
                                <Input
                                    type="date"
                                    placeholder={t("placeholders.from")}
                                    value={workExperienceDetails?.from}
                                    name="from"
                                    onChange={(e) => handleFromChange(e)}
                                    isDisabled={workExperience?.value === "fresher" ? true : false}
                                />

                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.to")} />
                                <Input
                                    type="date"
                                    placeholder={t("placeholders.to")}
                                    value={workExperienceDetails?.to}
                                    name="to"
                                    onChange={(e) => handleFromChange(e)}
                                    isDisabled={workExperience?.value === "fresher" ? true : false}
                                    min={workExperienceDetails?.from}
                                />

                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.designation")} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.enterDesignation")}
                                    value={workExperienceDetails?.designation}
                                    name="designation"
                                    onChange={(e) => handleFromChange(e)}
                                    isDisabled={workExperience?.value === "fresher" ? true : false}
                                />

                            </div>
                        </div>
                        <div className="col-md-4">
                            <div className="form-group mb-3">
                                <Label text={t("labels.reasonOfLeaving")} />
                                <Input
                                    type="text"
                                    placeholder={t("placeholders.reasonOfLeaving")}
                                    value={workExperienceDetails?.reasonOfLeaving}
                                    name="reasonOfLeaving"
                                    onChange={(e) => handleFromChange(e)}
                                    isDisabled={workExperience?.value === "fresher" ? true : false}
                                />

                            </div>
                        </div>
                    </div>
                </div>
            ))
        )
    }


    useEffect(() => {
        if (profileData) {
            setWorkExperienceDetails(profileData?.work_experience_details)
        }
    }, [profileData])

    return (
        <> <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
            tabIndex={-1}
            style={{
                visibility: openModel ? 'visible' : 'hidden',
            }}
            id="workexpDetails"
        >
            <div class="offcanvas-header">
                <h5 class="offcanvas-title  fs-6 fw-bold text-primary">Work Experience Details</h5>
                <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                    onClick={() => handleOpenClose("workExperienceDetail")}
                />

            </div>
            <div class="offcanvas-body pt-0 mt-3">
                <div className="formPanel">
                    <div className="detailMain">
                        <div className="personalDetail d-flex align-items-center">
                            <span onClick={() => handleNextPreOnClick("referenceDetail")}>
                                <img src="images/previousIcon-blue.svg" width="24" alt="" />
                            </span>
                            <div className="personalLeft ms-2">
                                <span>Work Experience Details</span>
                                <p>Lorem Ipsum is simply dummy text of the printing</p>
                                <strong>Status: <b className="text-success">4 of 4</b></strong>
                            </div>
                        </div>
                        <div className="personalDetail d-flex align-items-center">
                            <div className="personalLeft border-0 me-2 text-end">
                                <span>NEXT</span>
                                <p className="m-0">Document Details</p>
                            </div>
                            <span onClick={() => handleNextPreOnClick("policyDetail")}>
                                <img src="images/nextIconblue.svg" width="24" alt="" />
                            </span>
                        </div>
                    </div>

                    {renderedWorkExperienceDetail()}
                </div>

            </div>
            <div class="offcanvas-footer">
                {/* <div class="d-flex align-items-center justify-content-end gap-3">
                    <a href="#" class="btn btn-org-gradient"> Save</a>
                    <a href="#" class="btn btn-secondary"> Back</a>
                </div> */}
            </div>
        </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default WorkExpDetail
