import React, { useState } from 'react'
import { Input, Label } from '../../commanComponent'
import { calculateFilledPercentage } from '../../../utils/common';
import { validateSchema } from '../../../utils/ValidationHelper';
import { addSocialDetailsSchema } from '../../../utils/ValidationSchema';

const SocialDetails = (props) => {
    const { openModel, handleOpenClose, handleNextPreOnClick } = props
    const [errors, setErrors] = useState({});
    const [isDisabled,setIsDisabled]=useState(true)
    const [socialDetails, setSocialDetails] = useState(
        {
            "linkedin_id": '',
            "facebook_id": '',
            "twitter_id": '',
            "instagram_id": '',
        }
    );

    const percentage = calculateFilledPercentage(socialDetails);

    const handleFormData = async (e) => {
        errors && setErrors({});
        const { name, value } = e.target
        setSocialDetails({ ...socialDetails, [name]: value })
    };

    const onSubmitForm = async () => {
        const validationResponse = await validateSchema(addSocialDetailsSchema, socialDetails);
        if (validationResponse.isValid) {
            setErrors({});
        } else {
            setErrors(validationResponse.errors);
        }
    };
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="socialDetails"
            >
                <div class="offcanvas-header">
                    <h5 class="offcanvas-title  fs-6 fw-bold text-primary">Social Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("socialDetail")}
                    />

                </div>
                <div class="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <div className="detailMain">
                            <div className="personalDetail d-flex align-items-center">
                                <span onClick={() => handleNextPreOnClick("bankDetail")}>
                                    <img src="images/previousIcon-blue.svg" width="24" alt="" />
                                </span>
                                <div className="personalLeft ms-2">
                                    <span>Social Details</span>
                                    <p>Lorem Ipsum is simply dummy text of the printing</p>
                                    <strong>Status: <b className="text-success">4 of 4</b></strong>
                                </div>
                            </div>
                            <div className="personalDetail d-flex align-items-center">
                                <div className="personalLeft border-0 me-2 text-end">
                                    <span>NEXT</span>
                                    <p className="m-0">Reference Details</p>
                                </div>
                                <span onClick={() => handleNextPreOnClick("referenceDetail")}>
                                    <img src="images/nextIconblue.svg" width="24" alt="" />
                                </span>
                            </div>
                        </div>

                        <div className="mb-1">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="">LinkedIn ID</label>
                                        <Input
                                            type="text"
                                            name="linkedin_id"
                                            value={socialDetails?.linkedin_id}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter LinkedIn ID"
                                            readOnly={isDisabled}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="">Instagram ID</label>
                                        <Input
                                            type="text"
                                            name="instagram_id"
                                            value={socialDetails?.instagram_id}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter Instagram ID"
                                            readOnly={isDisabled}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="">Facebook ID</label>
                                        <Input
                                            type="text"
                                            name="facebook_id"
                                            value={socialDetails?.facebook_id}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter Facebook ID"
                                            readOnly={isDisabled}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group mb-3">
                                        <label htmlFor="">Twitter ID</label>
                                        <Input
                                            type="text"
                                            name="twitter_id"
                                            value={socialDetails?.twitter_id}
                                            onChange={(e) => handleFormData(e)}
                                            className="form-control"
                                            placeholder="Enter Twitter ID"
                                            readOnly={isDisabled}
                                        />
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>

                </div>
                <div class="offcanvas-footer">
                    {/* <div class="d-flex align-items-center justify-content-end">
                        <a href="#" class="btn btn-secondary"> Update</a>
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default SocialDetails
