import React, { useEffect, useState } from 'react'
import { Input, Label } from '../../commanComponent'
import { calculateFilledPercentage } from '../../../utils/common';
import { CustomError } from '../../globalComponents';
import { useCustomTranslation } from '../../../translation';
import { addPersonalDetailsSchema } from '../../../utils/ValidationSchema';
import { validateSchema } from '../../../utils/ValidationHelper';
import moment from 'moment';
import { CircularProgressbar } from 'react-circular-progressbar';

const ProfileDetail = (props) => {
    const { handleOpenClose, openModel, handleNextPreOnClick, profileData } = props
    const { t } = useCustomTranslation()
    const [errors, setErrors] = useState({});
    const [isDisabled,setIsDisabled]=useState(true)
    const [personalDetailData, setPersonalDetailData] = useState(
        {
            "name": "",
            "dob": "",
            "gender": "",
            "marital_status": "",
            "personal_mobile": "",
            "personal_email": "",
            "esic_no": "",
            "uan_no": "",
            "pan_no": "",
            "aadhar_no": "",
            "blood_group": "",
            "emergency_mobile": "",
            "present_address": "",
            "present_city": "",
            "present_state": "",
            "present_postal_code": "",
            "permanent_address": "",
            "permanent_city": "",
            "permanent_state": "",
            "permanent_postal_code": "",
        }
    );

    const percentage = calculateFilledPercentage(personalDetailData);

    const handleFormData = async (e) => {
        const { name, value } = e.target
        setPersonalDetailData({ ...personalDetailData, [name]: value })
        setErrors(prevErrors => ({
            ...prevErrors,
            [name]: undefined,
        }));

    }

    const onSubmitForm = async () => {
        const validationResponse = await validateSchema(addPersonalDetailsSchema, personalDetailData);
        if (validationResponse.isValid) {
            setErrors({});
            // addPersonalDetails(personalDetailData)
            // onClickActive(2)
        } else {
            setErrors(validationResponse.errors);
        }
    }

    useEffect(() => {
        if (profileData) {
            setPersonalDetailData(
                {
                    "name": profileData?.name,
                    "dob":  profileData?.dob,
                    "gender": profileData?.gender,
                    "marital_status": profileData?.marital_status,
                    "personal_mobile": profileData?.personal_mobile,
                    "personal_email": profileData?.personal_email,
                    "esic_no": profileData?.esic_no,
                    "uan_no": profileData?.uan_no,
                    "pan_no": profileData?.pan_no,
                    "aadhar_no": profileData?.aadhar_no,
                    "blood_group": profileData?.blood_group,
                    "emergency_mobile": profileData?.emergency_mobile,
                    "present_address": profileData?.present_address,
                    "present_city": profileData?.present_city,
                    "present_state":profileData?.present_state,
                    "present_postal_code": profileData?.present_postal_code,
                    "permanent_address": profileData?.present_address,
                    "permanent_city": profileData?.permanent_city,
                    "permanent_state": profileData?.permanent_state,
                    "permanent_postal_code": profileData?.permanent_postal_code,
                })
        }
    }, [profileData])
    return (
        <>
            <div className={`offcanvas offcanvas-end applyLeaveCanvas  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                }}
                id="profileDetails"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-6 fw-bold text-primary">Profile Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose("personalDetail")}
                    />
                </div>
                <div className="offcanvas-body pt-0 mt-3">
                    <div className="formPanel">
                        <div>
                        <div className="detailMain">
                            <div className="personalDetail d-flex align-items-center">
                                <span><img src="images/previousIcon-blue.svg" width="24" alt="" /></span>
                                <div className="personalLeft ms-2">
                                    <span>Personal Details</span>
                                    <p>Lorem Ipsum is simply dummy text of the printing</p>
                                   
                                </div>
                            </div>
                            <div className="personalDetail d-flex align-items-center">
                                <div className="personalLeft border-0 me-2 text-end">
                                    <span>NEXT</span>
                                    <p className="m-0">Family Details</p>
                                </div>
                                <span onClick={() => handleNextPreOnClick("familyDetail")}>
                                    <img src="images/nextIconblue.svg" width="24" alt="" />
                                    
                                </span>
                            </div>
                            
                        </div>
                 
                        </div>
                        <div className="row">
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.name")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.name")}
                                        value={personalDetailData?.name}
                                        name="name"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />
                                    {errors.name && <CustomError
                                        text={errors.name}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.dob")} required={true} />
                                    <Input
                                        type="date"
                                        value={personalDetailData?.dob}
                                        name="dob"
                                        onChange={(e) => handleFormData(e)}
                                        max={moment()
                                            .subtract(18, 'years')
                                            .format('YYYY-MM-DD')}
                                            readOnly={isDisabled}
                                    />
                                    {errors.dob && <CustomError
                                        text={errors.dob}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.gender")} required={true} />
                                    <select className="form-select" disabled={isDisabled} required
                                        name='gender'
                                        value={personalDetailData?.gender}
                                        onChange={(e) => handleFormData(e)}>
                                        <option value="" hidden selected>Select Gender...</option>
                                        <option value="male">Male</option>
                                        <option value="female">Female</option>
                                        <option value="other">Other</option>
                                    </select>{errors.gender && <CustomError
                                        text={errors.gender} />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.maritalStatus")} required={true} />
                                    <select className="form-select" required disabled={isDisabled}
                                        name='marital_status'
                                        value={personalDetailData?.marital_status}
                                        onChange={(e) => handleFormData(e)}>
                                        <option value="" hidden selected>Select Marital Status...</option>
                                        <option value="single">Single</option>
                                        <option value="married">Married</option>
                                    </select>{errors.marital_status && <CustomError
                                        text={errors.marital_status} />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.personalMobileNo")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.personal_mobile")}
                                        value={personalDetailData?.personal_mobile}
                                        name="personal_mobile"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />

                                    {errors.personal_mobile && <CustomError
                                        text={errors.personal_mobile}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.personalRegisteredEmail")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.personalEmail")}
                                        value={personalDetailData?.personal_email}
                                        name="personal_email"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />
                                    {errors.personal_email && <CustomError
                                        text={errors.personal_email}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.panNo")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.panNo")}
                                        value={personalDetailData?.pan_no}
                                        name="pan_no"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />

                                    {errors.pan_no && <CustomError
                                        text={errors.pan_no}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.aadhaarNo")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.aadhaarNo")}
                                        value={personalDetailData?.aadhar_no}
                                        name="aadhar_no"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />

                                    {errors.aadhar_no && <CustomError
                                        text={errors.aadhar_no}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.existingUanNo")} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.existingUanNo")}
                                        value={personalDetailData?.uan_no}
                                        name="uan_no"
                                        onChange={(e) => handleFormData(e)}
                                        readOnly={isDisabled}
                                    />

                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.bloodGroup")} required={true} />
                                    <select className="form-select" disabled={isDisabled} required
                                        name='blood_group'
                                        value={personalDetailData?.blood_group}
                                        onChange={(e) => handleFormData(e)}>
                                        <option value="" hidden selected>Select Blood Group...</option>
                                        <option value="O postive(+)">O positive(+)</option>
                                        <option value="O negative(-)">O negative(-)</option>
                                    </select>{errors.blood_group && <CustomError
                                        text={errors.blood_group} />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.existingEsiCNo")} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.existingEsiCNo")}
                                        value={personalDetailData?.esic_no}
                                        name="esic_no"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.emergencyMobileNo")} required={true} />
                                    <Input
                                        type="number"
                                        placeholder={t("placeholders.emergencyMobileNo")}
                                        value={personalDetailData?.emergency_mobile}
                                        name="emergency_mobile"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.emergency_mobile && <CustomError
                                        text={errors.emergency_mobile}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.presentAddress(Street)")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.presentAddress(Street)")}
                                        value={personalDetailData?.present_address}
                                        name="present_address"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />

                                    {errors.present_address && <CustomError
                                        text={errors.present_address}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.presentCity")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.presentCity")}
                                        value={personalDetailData?.present_city}
                                        name="present_city"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.present_city && <CustomError
                                        text={errors.present_city}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.presentState")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.presentState")}
                                        value={personalDetailData?.present_state}
                                        name="present_state"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.present_state && <CustomError
                                        text={errors.present_state}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.presentPostalCode")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.presentPostalCode")}
                                        value={personalDetailData?.present_postal_code}
                                        name="present_postal_code"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.present_postal_code && <CustomError
                                        text={errors.present_postal_code}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-12">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.permanentAddress(Street)")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permanentAddress(Street)")}
                                        value={personalDetailData?.permanent_address}
                                        name="permanent_address"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.permanent_address && <CustomError
                                        text={errors.permanent_address}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.permanentCity")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permanentCity")}
                                        value={personalDetailData?.permanent_city}
                                        name="permanent_city"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />

                                    {errors.permanent_city && <CustomError
                                        text={errors.permanent_city}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.permanentState")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permanentState")}
                                        value={personalDetailData?.permanent_state}
                                        name="permanent_state"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.permanent_state && <CustomError
                                        text={errors.permanent_state}
                                    />}
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="form-group mb-3">
                                    <Label text={t("labels.permanentPostalCode")} required={true} />
                                    <Input
                                        type="text"
                                        placeholder={t("placeholders.permanentPostalCode")}
                                        value={personalDetailData?.permanent_postal_code}
                                        name="permanent_postal_code"
                                        readOnly={isDisabled}
                                        onChange={(e) => handleFormData(e)}
                                    />
                                    {errors.permanent_postal_code && <CustomError
                                        text={errors.permanent_postal_code}
                                    />}
                                </div>
                            </div>
                            {/* <div className="d-flex justify-content-end mt-3 mb-3">
                <CustomButton
                  text={"Save & Next"}
                  classNames="btn btn-org-gradient"
                  handleClick={() => onSubmitForm()}
                />
              </div> */}
                        </div>
                    </div>

                </div>
                <div className="offcanvas-footer">
                    {/* <div className="d-flex align-items-center justify-content-between gap-3">
                        <span className="adharVerification"> <i className="bi bi-info-circle pe-2"></i> Requires Aadhar
                            verification</span>
                        <a href="" data-bs-toggle="offcanvas" className="btn btn-org-gradient"><img src="images/aw" alt=""
                            width="20" /> Edit</a>
                    </div> */}
                </div>
            </div>
            {openModel && <div className="modal-backdrop fade show"></div>}
        </>
    )
}

export default ProfileDetail
